<template>
  <div class="zp">
    <img src="../assets/bg.png" class="bg" :style="runstatus?'transform:rotate('+m+'deg)':'transition:none'">
    <img src="../assets/zz.png" class="zz" @click="run()">
    <div class="again" v-if="seestatus" @click="Reset()">
      再来一次
    </div>
  </div>
</template>
<script>
import { Toast } from 'mint-ui';
import "mint-ui/lib/style.min.css";

export default {
  name: "zp",
  data() {
    return {
      runstatus: false,
      seestatus: false,
      m: 0,
      timestamp: ""
    };
  },
  methods: {
    run() {
      if (!this.timestamp) {
        this.timestamp = Date.parse(new Date());
      } else if (Date.parse(new Date()) - this.timestamp <= 5000) {
        return;
      }
      let m = Math.ceil(this.getnub() * 10000);
      this.m = m;
      // console.log(m);
      this.runstatus = true;
      if (this.runstatus) {
        this.getdeg(m);
      }
    },
    Reset() {
      this.runstatus = false;
      this.seestatus = false;
    },
    getnub() {
      let m = Math.random();
      if (m < 0.5) {
        return this.getnub();
      } else {
        return m;
      }
    },
    getdeg(m) {
      let n = m % 360;
      setTimeout(() => {
        if (n < 45) {
          Toast("奖励大佬蛋糕一份");
        } else if (n < 90) {
          Toast("奖励大佬番茄炒蛋一份");
        } else if (n < 135) {
          Toast("奖励大佬奶茶一杯");
        } else if (n < 180) {
          Toast("奖励大佬糖醋里脊一份");
        } else if (n < 225) {
          Toast("奖励大佬发糕一份");
        } else if (n < 270) {
          Toast("奖励大佬猪肝面一碗");
        } else if (n < 315) {
          Toast("奖励大佬橙汁一杯");
        } else if (n < 360) {
          Toast("奖励大佬蛋炒饭一份");
        }
        this.seestatus = true;
      }, 5000);
    }
  }
};
</script>
<style scoped lang="scss">
.zp {
  text-align: center;
}
.bg {
  width: 7.5rem;
  height: 7.5rem;
  transition: transform 5s ease;
}
.zz {
  width: 7.5rem;
  height: auto;
  position: relative;
  top: -7.55rem;
  z-index: 10;
}
.again {
  z-index: 100;
  position: fixed;
  top: 3.32rem;
  left: 3.28rem;
  background-color: #ca1518;
  color: white;
  font-size: 0.3rem;
  width: 0.6rem;
  padding: 0.1rem 0.2rem;
  border-radius: 0.5rem;
}
</style>


